body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.card__buy-link {
  width: 166px;
  text-decoration: none;
  display: block;
}

.card-element {
  width: 166px;
}

.rate-review {
  text-align: right;
  color: #060b35;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price-amount {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.buy-button {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.buy-button:hover {
  background-color: inherit;
  color: #00acdc;
  border: 1px solid #00acdc;
  line-height: 38px;
}

.stars {
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

